"use client";
import { useQuery } from "@tanstack/react-query"
import { getProducts } from "@/lib/graphql/graphql";


export function ImagesView(props: { imgs: String[] }) {

    // const { data, error } = useQuery({
    //     queryKey: ["products"],
    //     queryFn: () => getProducts(),
    // });

    function renderImgs(){
        return props.imgs.map( img => <img src={String(img)} alt="" className="h-full w-auto cursor-pointer"/>);
    }

    return (
        <div className="bg-black w-[100vw] h-[max(70vh,20rem)] max-w-[var(--max-w)] max-h-[calc(var(--max-h)*60/100)] overflow-x-slide overflow-y-clip">
            <button className="corousel-arrow">❮</button>
            {renderImgs()}
            <button className="corousel-arrow">❯</button>
        </div>
    );
}